import * as React from 'react';
import Card from '../Card';
import Section from '../Section';
import { Flex } from '@chakra-ui/core';

const icons = ['ideas', 'structure', 'rent'];

export default function Description({ data, ...props }) {
  return (
    <Section {...props}>
      <Flex wrap="wrap" mx={[2, 8, 24]}>
        {data.map((card, index) => (
          <Card key={card.id} card={card} icon={icons[index]} index={index} />
        ))}
      </Flex>
    </Section>
  );
}
